import { initial_fashion_search_product_arr } from '../../../../../_initial/fashion/FashionInitial';
//
import { handle_API_FsSearch_L } from '../../../../../_handle_api/fashion/FashionHandleAPI';

//
export const FsSearch_initial_state_obj = () => {
    return {
        product_arr: initial_fashion_search_product_arr(),

        area_arr: [
            { title: 'Ha Noi', checked: false },
            { title: 'HCM', checked: false },
            { title: 'Da Nang', checked: false },
        ],

        page: 1,
        pages: 1,
        value_search: '',
        rate_ix: 0,
        sort_ix: -1,

        has_fetched: false,
        is_fetching: false,
        open_filter: false,
    };
};

//
export async function FsSearch_getData_API({
    params = {
        new_value_search,
        new_page,
        new_rate_ix,
        new_sort_by,
        new_area_arr,
    },
    state_obj = FsSearch_initial_state_obj(),
    setStateObj = () => {},
}) {
    const { value_search, page, rate_ix, sort_by, area_arr } = state_obj;
    const {
        new_value_search = value_search,
        new_page = page,
        new_rate_ix = rate_ix,
        new_sort_by = sort_by,
        new_area_arr = area_arr,
    } = params;

    setStateObj((state_obj) => {
        return {
            ...state_obj,
            rate_ix: new_rate_ix,
            area_arr: new_area_arr,
            sort_by: new_sort_by,
            is_fetching: true,
        };
    });

    const { data, pages: new_pages } = await handle_API_FsSearch_L({
        page: new_page,
        q: new_value_search,
        area: new_area_arr
            .filter((item) => item.checked)
            .map((item) => item.title),
        rate: new_rate_ix,
        sort_by: new_sort_by,
    });

    setStateObj((state_obj) => ({
        ...state_obj,
        product_arr: data,

        page: new_page,
        pages: new_pages,
        value_search: new_value_search,

        is_fetching: false,
        has_fetched: true,
    }));
}
